'use strict';
// ab 对照用户

/**
 * todo TOOLS
 */
import {WinJSDec, appError, fnError} from './APP/config';

var name = 'AB_GROUP';
var AB_GROUP = (ruleId, fn = _ => {
}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      window.WinJSBridge.call('abtest', 'getGroup', {ruleId}, res => {
        var {code, response} = res;
        if (code == 0) {
          fn(response);
        } else {
          throw new Error(`[${name}] ${appError}`);
        }
      });
    });
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default AB_GROUP;
